#button {
    width: 440px;
    position: relative;
}

#button span {
    font-size: 0.7em;
    position: relative;
}

.b_text {
    text-align: center;
    color: #111;
    position: absolute;
    white-space: nowrap;
}

.b_text i:after, .b_text i:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    -webkit-box-shadow: inset 0 3px 5px hsla(0,0%,100%,.8);
            box-shadow: inset 0 3px 5px hsla(0,0%,100%,.8);
}

.b_text i:after {
    -webkit-box-shadow: inset 0 -3px 5px rgba(0,0,0,.8);
            box-shadow: inset 0 -3px 5px rgba(0,0,0,.8);
}

.b_text i {
    display: block;
    position: relative;
    border: 1px solid #000;
    border-radius: 50%;
    -webkit-box-shadow: 0 3px 3px rgba(0,0,0,.2);
            box-shadow: 0 3px 3px rgba(0,0,0,.2);
}

.b_dire i {
    width: 70px;
    height: 70px;
}

.b_space i {
    width: 100px;
    height: 100px;
}

.b_control i {
    width: 40px;
    height: 40px;
    margin-left: 4px;
}

.b_bg1 i {
    background: #5a65f1;
}

.b_bg2 i {
    background: #9ef15a;
}

.b_bg3 i {
    background: #ff4d21;
}

.b_dire em {
    width: 0;
    height: 0;
    border: 8px solid;
    border-color: transparent transparent #111;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -12px 0 0 -8px;
}

#rotate {
    top: -16px; 
    right: 110px;
}

#rotate em {
    -webkit-transform: translate(0px, 52px) scale(0.75, 1.5);
            transform: translate(0px, 52px) scale(0.75, 1.5);
}

#down {
    top: 125px;
    right: 110px;
}

#down em {
    -webkit-transform: translate(0px, -45px) rotate(180deg) scale(0.75, 1.5);
            transform: translate(0px, -45px) rotate(180deg) scale(0.75, 1.5);
}

#left {
    top: 65px;
    right: 171px;
}

#left em {
    -webkit-transform: translate(40px, -8px) rotate(270deg) scale(0.75, 1.5);
            transform: translate(40px, -8px) rotate(270deg) scale(0.75, 1.5);
}

#right {
    top: 65px;
    right: 48px;
}

#right em {
    -webkit-transform: translate(-40px, -8px) rotate(90deg) scale(0.75, 1.5);
            transform: translate(-40px, -8px) rotate(90deg) scale(0.75, 1.5);
}

#drop {
    top: 80px;
    left: 72px;
}

#pause {
    top: 10px;
    left: 60px;
}

#reset {
    top: 10px;
    left: 135px;
}

i.pressed:after {
    -webkit-box-shadow: inset 0 3px 4px rgba(0,0,0,.6);
            box-shadow: inset 0 3px 4px rgba(0,0,0,.6);
}

i.pressed:before {
    -webkit-box-shadow:  inset 0 -2px 3px hsla(0,0%,100%,.6);
            box-shadow:  inset 0 -2px 3px hsla(0,0%,100%,.6);
}

#info {
    width: 28%;
    position: absolute;
    right: 3%;
    top: 0;
}

#info p {
    font-size: 0.9em;
    line-height: 50px;
}

#info span {
    float: right;
    width: 10px;
    height: 16px;
    overflow: hidden;
    background: url(data:image/png;base64,) no-repeat;    
}

#info .default, #info span {
    background-position: -161px -20px;
}

#info .n0, #s_hour span, #s_minute span {
    background-position: -56px -20px;
}

#info .n1 {
    background-position: -66px -20px;
}

#info .n2 {
    background-position: -77px -20px;
}

#info .n3 {
    background-position: -88px -20px;
}

#info .n4 {
    background-position: -98px -20px;
}

#info .n5 {
    background-position: -108px -20px;
}

#info .n6 {
    background-position: -119px -20px;
}

#info .n7 {
    background-position: -129px -20px;
}

#info .n8 {
    background-position: -140px -20px;
}

#info .n9 {
    background-position: -150px -20px;
}

#next {
    margin-top: 1.5em;
}

#next canvas {
    margin-left: 0.125em;
}

#status {
    width: 100%;
    margin-top: 40%;
}

#status #s_psign {
    width: 14px;
    height: 13px;
    float: left;
    margin-right: 0px;
    margin-top: 2px;
}

#info .unpaused {
    background-position: -76px -57px;
}

#info .paused {
    background-position: -57px -57px;
}

#info .tik {
    background-position: -173px -19px;
}

#info .tok {
    background-position: -183px -19px;
}

#root {
    width: 440px;
    height: 100%;

    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    
    background: #ebcb3c;
    border-radius: 1em;
    position: absolute;
    -webkit-box-shadow: inset 0 0 10px #fff;
            box-shadow: inset 0 0 10px #fff;
}

#game {
    border: solid #000;
    border-width: 0 6px 6px;
    margin: 30px 50px 15px;
    width: 340px;
    padding: 30px 0 20px;
    position: relative;
}

#container {
    width: 86%;
    border: 5px solid;
    border-color: #987f0f #fae36c #fae36c #987f0f;
    margin: 0 auto;
    position: relative;
}

#border {
    background: #9ead86;
    border: 1px solid #000;
    padding: 4px 4px 0px;
}

#main canvas {
    border: 2px solid #000;
    width: 65%;
}

#head , #head h1 {
    position: absolute;
    width: 100%;
    height: 6px;
    top: 0;
}

span[class^=end] {
    width: 15%;
    height: 6px;
    background: #000;
}

span.end1 {
    float: left;
    margin-right: 6px;
}

span.end2 {
    float: right;
    margin-left: 6px;
}

#head span[class^=dot] {
    background: #000;
    width: 6px;
    height: 6px;
}

#head span.dot1 {
    float: left;
    margin-right: 6px;
}

#head span.dot2 {
    float: right;
    margin-left: 6px;
}

#title {
    font-size: 1.2em;
    font-weight: 500;
    position: absolute;
    top: 20%;
    left: 18%;
    visibility: hidden;
}

#head h1 {
    font-size: 1.4em;
    font-weight: 400;
    text-align: center;
    top: -0.6em;
}

* {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    margin: 0;
    padding: 0;

    /* 阻止长按选中文本 */
    -webkit-touch-callout: none;
    
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;

    /* 阻止点击出现阴影 */
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
}

div {
    display: block;
}

html, body {
    height: 100%;
}

body {
    overflow: hidden;
    font-size: 16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    cursor: default;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    direction: ltr;
    text-align: left;
}

#guide {
  height: 0;
}

.guide-op {
  position: relative;
  display: inline-block;
  background: #364d4b;
  border-radius: 4px;
  color: #acc3c1;
  font-size: 16px;
  -webkit-box-shadow: inset 0 5px 10px hsla(0,0%,100%,.15);
          box-shadow: inset 0 5px 10px hsla(0,0%,100%,.15);
  
  height: 40px;
  width: 70px;
  top: 140px;
}

.guide-op i {
  display: block;
  width: 0;
  height: 0;
  border: 5px solid;
  border-color: transparent transparent #acc3c1;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -9px 0 0 -5px;
}

.guide-op:before, .guide-op:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 5px 10px hsla(0,0%,100%,.15);
          box-shadow: inset 0 5px 10px hsla(0,0%,100%,.15);
}

.guide-op:before {
  -webkit-box-shadow: inset 0 -5px 10px rgba(0,0,0,.15);
          box-shadow: inset 0 -5px 10px rgba(0,0,0,.15);  
}

.guide-op.space {
  text-align: center;
  font-weight: 600;
  left: -80%;
  height: 60px;
  width: 300px;
  line-height: 60px;
  letter-spacing: 2px;
}

.guide-op-wrapper {
  position: absolute;
  left: 110%;
  top: -25px;
  white-space: nowrap;
}

.guide-op.up {
  margin: auto;
  display: block;
  margin-bottom: 4px;
}